.header {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 50px;
  padding: 9px 10px 0;
  background-color: #13141c; }
  @media (min-width: 375px) {
    .header {
      padding-right: 16px; } }

.header__inner {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center; }

.header__logo {
  width: 34.5px;
  height: 29.2px;
  margin-right: 3px;
  color: #2068ff; }
  .header__logo svg {
    fill: currentColor; }
  @media (min-width: 375px) {
    .header__logo {
      margin-right: 13px; } }

.header__btn {
  position: absolute;
  top: 3px;
  right: 0;
  height: 26px;
  padding: 0 5px;
  color: #fff;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  border-radius: 4px;
  border: solid 1px #69728b;
  opacity: .7;
  cursor: pointer;
  -webkit-transition: opacity .2s;
  -o-transition: opacity .2s;
  transition: opacity .2s; }
  .header__btn:hover {
    opacity: 1; }
  @media (min-width: 375px) {
    .header__btn {
      width: 68px; } }



