.login {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  padding: 15px 18px 70px 13px;
  background-color: #181925;
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  -o-transition: transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s; }
  .login.is-hide {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0); }

.login__inner {
  max-height: 100%;
  overflow: auto; }

.login__header {
  position: relative; }

.login__title {
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  line-height: 1; }

.login__switch {
  position: absolute;
  top: 3px;
  right: 0;
  color: #615f6a;
  font-size: 14px; }
  .login__switch span {
    color: #fff;
    cursor: pointer;
    -webkit-transition: opacity .2s;
    -o-transition: opacity .2s;
    transition: opacity .2s; }
    .login__switch span:hover {
      opacity: .5; }

.login__form {
  margin-top: 10px; }

.login__field:not(:last-child) {
  margin-bottom: 17px; }

.login__submit {
  position: absolute;
  bottom: 22px;
  left: 13px;
  right: 18px; }



