.field {
  position: relative;
  border-bottom: solid 1px #2f3751; }
  .field.is-active {
    background-color: rgba(0, 0, 0, 0.2); }

.field__input {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 48px;
  padding-top: 28px;
  color: #fff;
  font-size: 14px;
  border: none;
  background-color: transparent; }

.field__label {
  position: absolute;
  z-index: 1;
  bottom: 2px;
  left: 0;
  color: #69728b;
  font-size: 14px;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  -o-transition: transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s; }
  .is-active .field__label {
    -webkit-transform: translate3d(0, -25px, 0);
            transform: translate3d(0, -25px, 0); }

.field__link {
  position: absolute;
  top: 100%;
  top: calc(100% + 3px);
  right: 0;
  color: #69728b;
  font-size: 12px;
  cursor: pointer;
  -webkit-transition: opacity .2s;
  -o-transition: opacity .2s;
  transition: opacity .2s; }
  .field__link:hover {
    opacity: .5; }

.field__btns {
  position: absolute;
  z-index: 3;
  bottom: 6px;
  right: 0; }

.field__btn {
  display: inline-block;
  cursor: pointer;
  color: #626a82; }
  .field__btn svg {
    fill: currentColor; }
  .field__btn:not(:last-child) {
    margin-right: 10px; }
  .field__btn:hover {
    color: #fff; }



